/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><img src=\"http://papascott-de.s3.amazonaws.com/wordpress/wp-content/uploads/2001/09/s1poster.jpg\" width=\"200\" height=\"279\" hspace=\"10\" vspace=\"5\" border=\"0\" align=\"right\" />II debated whether to post anything for the 10th anniversary of 9/11. I've pretty much written everything I have to say about it, either <a href=\"http://www.papascott.de/archives/2001/09/12/the-day-the-earth-stood-still/\">a day later in 2001</a>, or <a href=\"http://www.papascott.de/archives/2006/09/10/five-years/\">5 years ago</a>, or even just <a href=\"http://www.papascott.de/archives/2009/09/11/inner-peace/\">2 years ago</a>.</p>\n<p>I was an expat American in Hamburg in 2001, the city where the evil had spawned, and perhaps I was much closer to that evil than I really wanted to know. <a href=\"http://www.papascott.de/archives/2001/09/13/were-in-this-together/\">\"Amerikaner, wir sind bei euch\"</a> (Americans, we stand by you) was the headline in the Hamburg paper the next day, and that was certainly comforting at the time. The only lasting consequence of that solidarity seems to be the military quagmire in Afghanistan.</p>\n<p><a href=\"http://www.papascott.de/archives/2001/09/16/sunday-at-home/\"><img src=\"http://papascott-de.s3.amazonaws.com/wordpress/wp-content/uploads/2001/09/hhcandles.jpg\" height=\"235\" width=\"345\" border=\"0\" alt=\"hhcandles.jpg: \" /></a></p>\n<p>As for these candles at the US Consulate in Hamburg, I don't think you can even get that close to consulate anymore. The street is closed, last time I was there we had to go past guards in <a href=\"http://www.papascott.de/archives/2002/11/02/terror-begins-at-home/\">tanks with machine guns</a> to get in. Maybe that's all I need to say about how the world is different today.</p>\n<p>All in all I'd just rather <a href=\"http://www.youtube.com/watch?v=52mgnN9xfvA\">forget that day</a>.</p>\n<p><iframe width=\"480\" height=\"390\" src=\"http://www.youtube.com/embed/52mgnN9xfvA\" frameborder=\"0\" allowfullscreen></iframe></p>","fields":{"title":"The End of the Century","date":"11 Sep 2011","url":"https://papascott.de/2011-09-11-the-end-of-the-century/"}}},"pathContext":{"slug":"/2011-09-11-the-end-of-the-century/","prev":{"fields":{"title":"28 years ago","date":"06 Sep 2011","slug":"/2011-09-06-28-years-ago/"}},"next":{"fields":{"title":"Off The Rack","date":"02 Oct 2011","slug":"/2011-10-02-off-the-rack/"}}}}

/***/ })

